* {
  margin: 0;
  padding: 0;
}
.main {
  padding: 10px;
  font-family: Arial, Helvetica, sans-serif;
}

.container-fluid {
  width: 100%;
  margin: 10px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: 10px;
  margin-left: 10px;
}

.btn {
  display: inline-block;
  font-weight: 400;
  color: #212529;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.btn-group,
.btn-group-vertical {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.btn-group-vertical > .btn,
.btn-group > .btn {
  position: relative;
  float: left;
}

/* Green */

.success {
  border-color: #04aa6d;
  color: green;
}
.btn-secondary {

  border-color: #ffffff;
  background-color: #555;
}
.btn-secondary:hover {
  border-width: 2px;
  border-color: cyan;
  color: #000000;
  background-color: #ffffff;
}

.success:hover {
  background-color: #04aa6d;
  color: white;
}

.btn-block {
  display: block;
}

/* Blue */
.btn-info {
  border-color: #2196f3;
  background-color: #2196f3;
  color: #ffffff;
}

.btn-info:hover {
  background: #2196f3;
  color: white;
}

/* Orange */
.warning {
  border-color: #ff9800;
  color: orange;
}

.warning:hover {
  background: #ff9800;
  color: white;
}

.block {
  width: 95%;
}

/* Red */
.danger {
  border-color: #f44336;
  color: red;
}

.danger:hover {
  background: #f44336;
  color: white;
}

.default:hover {
  background: #e7e7e7;
}

.outline {
  border-radius: 5px;
  color: #000;
  background-color: #ffffff;
}


.nav-item {
  /* margin-top: -15px; */
  /* margin-left: -12px; */
  margin: 5px;
  width: 100%;
  border-radius: 5px;
  padding: 5px ;
  display: -webkit-flex;
  display: flex;
  background-color: rgb(4, 5, 5);
  -webkit-align-items: center;
          align-items: center;
}
.nav-item li {
  padding: 5px ;
  border-radius: 4px;
  list-style: none;
  padding: 5px 5px;
 
}
.nav-link {
  padding: 5px ;
  width: 100%;
  margin: 10px;
  border-radius: 4px;
  text-decoration: none;
  color: #ffff;
}
.nav-link:hover {
  background-color: white;
  color: #000;
  margin: 10px;
  padding-right: 15px;
  padding-left: 15px;
  border-radius: 4px;
  /* width: 100%; */
  border: 2px solid black;
}

.input {
  width: 100%;
  margin: 5px;
  padding: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
}
.input:hover {
  width: 110%;
  height: 120%;
  margin: 5px;
  padding: 10px;
  background-color: lightcyan;
  border: 2px solid rgb(7, 5, 5);
  border-radius: 5px;
}


hr {
  border: 0;
  border-top: 2px solid #eee;
  margin-left: 5px;
  margin-right: 5px;
}
.card {
  position: relative;
  display: -webkit-flex;
  display: flex;
  /* padding: 10px; */
  -webkit-flex-direction: column;
          flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 1.1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
}
